.background {
  background: url("../assets/images/main_bg.jpg") no-repeat;
  background-attachment: fixed;
  background-size: cover;
  //background: red;
}

.frost-glass {
  border-radius: 5px;
  position: relative;
  z-index: 1;
  background: inherit;
  overflow: hidden;
  &:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(17px);
    margin: -20px;
  }
  &.with-shadow {
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    &:before {
      box-shadow: inset 0 0 2000px rgba(255, 255, 255, 0.5);
    }
  }
}

.d-flex {
  display: flex;
}

.no-bg {
  background: none !important;
}
